<template>
  <div>
    <mrc_ess_table v-if="$route.name=='businessApply'"
                   :headers='headers'
                   pageName='addBusinessApply'
                   :paginationOptions='options'
                   @turnPage='getlist'
                   :isLoading='loading'
                   :slotKey="['name','Date']"
                   :items='desserts'>

      <template v-slot:top>
        <v-card-title class="cardtitle"
                      style="height:60px">
          <mrc_ess_searchInput :isShow='true'
                               style="position:absolute;right:10px;top:18px;"></mrc_ess_searchInput>
        </v-card-title>
      </template>

      <!-- <template v-slot:item.name='{item}'>
        <mrc_ess_staffSpan :list='item'
                           class="mt-3 mb-3"></mrc_ess_staffSpan>
      </template> -->
      <template v-slot:item.Date='{item}'>
        {{item.effective_start|changeDate}}-{{item.effective_end|changeDate}}
      </template>
      <!-- 操作 -->
      <template v-slot:item.actions='{item}'>
        <menu-list :list='menulist'
                   @handleClick='handleClick($event,item)'>
          <template v-slot:showAction>
            <v-icon>mdi-dots-vertical</v-icon>
          </template>
        </menu-list>
      </template>
    </mrc_ess_table>
    <router-view v-else></router-view>
  </div>
</template>

<script>
import {
  getBusinessApplyList
  , addBusinessApply
  , updateBusinessApply
  , deleteBusinessApply
  , detailBusinessApply
} from '@/service/api/businessApply.js'
export default {
  components: {

  },
  props: {},
  data () {
    return {
      cx: 15015255512222,
      stepIndex: 1,
      stepList: [
        {
          name: '导入文件'
        },
        {
          name: '结果'
        },
      ],
      dialog: false,
      disabled: true,
      list: {},
      options: {
        pagesize: 5,//条数
        cpage: this.$store.state.cpage,  //页数
        totalCount: null, //总条数
        totalpage: null, //总页数
      },
      loading: false,
      desserts: [],
    };
  },
  computed: {
    headers () {
      return [
        { text: '被代理员工', align: 'start', value: 'uuid', sortable: false },
        { text: '日期', value: 'Date', sortable: false },
        { text: '', value: 'actions', sortable: false, align: 'end', },
      ]
    },
    menulist () {
      return [
        { name: this.$t('label_View') },
        { name: this.$t('label_Edit') },
        { name: this.$t('label_delete') },
      ]
    }
  },
  created () { },
  watch: {
    $route (v) {
      if (v.name == 'businessApply') {
        this.getlist()
      }
    }
  },
  mounted () {
    this.getlist()
  },
  beforeDestroy () {
    this.$store.commit('setCpage', 1)
  },
  methods: {
    async getlist (obj) {
      this.loading = true
      if (this.desserts.length == 1 && this.options.cpage != 1) this.options.cpage--
      if (obj) {
        const { cpage, pagesize } = obj
        this.options = obj
      }
      const res = await getBusinessApplyList({
        cpage: this.options.cpage, pagesize: this.options.pagesize
      })
      if (res.code == 200) {
        this.desserts = res.data
        this.options.totalCount = res.totalCount
        this.options.totalpage = res.totalpage
      }
      this.loading = false
    },
    addStaffLeave () {
      this.$store.commit('set_subPage_name', '')
      this.$store.commit('set_uuid', '')
      this.$store.commit('set_iconstatus', '')  //行内进图标显示
      this.$router.push({
        name: 'addBusinessApply'
      })
    },
    godetail (item) {
      this.$store.commit('set_iconstatus', true)  //行内进图标显示
      // this.$store.commit('set_subPage_name', item.job_name)
      this.$store.commit('set_uuid', item.uuid)  //
      this.$router.push({
        name: 'detailBusinessApply'
      })
    },
    handleClick ({ index }, item) {
      switch (index) {
        case 0:
          this.godetail(item)
          break
        case 1:
          // this.$store.commit('set_subPage_name', item.job_name)
          this.$store.commit('set_iconstatus', false)  //行内进图标显示 
          this.$store.commit('set_uuid', item.uuid)  //
          this.$router.push({
            name: 'detailBusinessApply'
          })
          break
        case 2:
          this.$myConfirm({ content: this.$t('message_Confirm_the_deletion') }).then(res => {
            deleteStaffLeave({ uuid: item.uuid }).then(res => {
              if (res.code === '200') {
                this.$Message({ text: this.$t('message_The_deletion_was_successful') })
                if (this.desserts.length = 1) this.options.cpage--
                this.getlist()
              }
            })
          })
          break
      }
    },
    success () { },
    cancel () { }
  },
};
</script>

<style scoped>
.cardtitle {
  position: relative;
}
.emailbtn {
  position: absolute;
  right: 70px;
  top: -50px;
}
.dowanTemplate {
  position: absolute;
  right: 180px;
  top: -50px;
}
</style>
